<template>
  <div id="app">
     <div class="row">
       <div class="col s6 offset-s3">
          <div class="card blue-grey darken-1">
            <div class="card-content">
              <span class="card-title white-text">Dially Report Tasks Task</span>
              <div class="row">
                <div class="input-field col s5">
                  <input id="Your Tasks" type="text" v-model="inpTask">
                  <label class="active" for="first_name2">Tasks</label>
                </div>
                
                <div class="col s2">
                      <button @click="addTask" style="margin-top:40px;font-size:20px;font-family: Arial, Helvetica, sans-serif">Add</button>
                </div>
              </div>
              <div class="row">
                <div class="col">
                  <p style="color:red">{{error}}</p>
                </div>
              </div>
            </div>
          <div class="divider"></div>
          <div class="row" v-for="(item, index) in tasklists" :key="index">
            <div class="divider"></div>
            <p class="col s1 white-text">{{index}}</p>
            <p class="col s4 white-text">{{new Date(item.dateCreateed).getFullYear()}}-{{new Date(item.dateCreateed).getMonth()}}-{{new Date(item.dateCreateed).getDate()}} </p>
            <p class="col s3 white-text">{{item.task}}</p>
            <div class="col s4"><button @click="deleteTask(item._id)" style="margin-top:10px;font-family: Arial, Helvetica, sans-serif;margin-left:50px" class="red-text">Delete</button></div>
            
          </div>
       </div>
      </div>      
    </div>
  </div>
</template>

<script>
// import TaskService from './TaskService'
import '../node_modules/materialize-css/dist/css/materialize.min.css'
import '../node_modules/materialize-css/dist/js/materialize.min.js'
import axios from 'axios';
export default {
  name: 'App',
  data(){
    return{
      tasks:[],
      tasklists:[],
      inpTask:'',
      error:''
    }
  },


  methods:{
    deleteTask:function(id){
       axios.delete(`api/tasklist/${id}`)
       .then(() => {
          alert('Delete Successfully....')
        })
        .catch(e => {
          this.errors.push(e)
        })
        
        location.reload();
    },
    addTask:function(){
      if(this.inpTask ==''){
        this.error = 'Please input Your Task'
        
      }else{
       axios.post('api/tasklist',
      {
        task:this.inpTask
      })
       .then(response => {
          console.log(response.data)
        })
        .catch(e => {
          this.errors.push(e)
        })
      location.reload();
        }
        
    }
  },
  created(){

    axios.get('api/tasklist')
    .then(response => {
      // JSON responses are automatically parsed.
      this.tasklists = response.data
    })
    .catch(e => {
      this.errors.push(e)
    })
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.task-list{
  margin-right: 150px;
}
</style>
